import React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import { InputAdornment } from '@mui/material';

const TableForm = (props) => {
    const { label = "none", txtInfo=[] } = props;
    let feildLen = txtInfo.length;
    const colSize = feildLen > 1 ? (8 / feildLen) : 8;
    let labelColSize = txtInfo ? 3 : 12;
    
    return (
        <React.Fragment>
            <Grid container 
                 spacing={2} padding={0} 
                 flexdirection="row" justify="center" justifyContent="center" 
                 alignItems="ceneter">
                
                <Grid item xs={labelColSize} sm={labelColSize} lg={labelColSize}>
                    <Typography color="primary" align="left" 
                        sx={{ mt: 2.3, fontSize: "0.9em", fontWeight: 500}} >
                        {label}
                    </Typography>
                </Grid>

                {txtInfo.map((info, idx) => {
                    let variant = info.variant ? info.variant : "standard";
                    let { id, txtLabel, onChange, type, value, sx, InputProps, ...others } = info;
                    return  (
                    <Grid item key={idx} xs={colSize} sm={colSize}
                        sx={{
                            pb: 2,
                        }}
                    >
                        <TextField
                            id={id}
                            name={id}
                            label={txtLabel}
                            fullWidth
                            autoComplete="no info"
                            variant={variant}
                            onChange={onChange}
                            onFocus={ e => e.target.select() }
                            type={type}
                            value={value}
                            sx={{
                                ...sx,
                            }}
                            InputProps = {{ 
                                endAdornment: <InputAdornment position="end">{info.endAdornment}</InputAdornment>,
                                inputMode: 'numeric', pattern: '[0-9]*',
                                ...InputProps,
                            }}
                            inputProps={{
                                style: {
                                  padding: '5px 10px',
                                },
                            }}
                            {...others}
                        />
                    </Grid>
                    )}
                    
                )}
            </Grid>
        </React.Fragment>
        );
    }

export default TableForm;