import React, { useContext, useEffect, useState } from 'react';
import TableForm from '../Components/TableForm';
import InfoTableContainer from './InfoTableContainer';
import { appContext } from './AppMain';
import TitleRow from './TitleRow';
import BasicTable from '../Components/BasicTable';
// import { COLOR_PRIMARY_LIGHT, COLOR_WHITE, DIVIDE_SLEEVE_COUNT } from '../utils/constants';


const SwatchTable = (props) => {
    let context = useContext(appContext);

    const [contractionSts, setContractionSts] = useState(0);
    const [contractionRows, setContractionRows] = useState(0);

    useEffect(() => {
        let beforeSts = context.beforeSts;
        let afterSts = context.afterSts;
        // 수축률 = 세탁 전 / 세탁 후 * 100
        if (beforeSts > 0 && afterSts > 0) {
            let res = (beforeSts / afterSts) * 100;
            setContractionSts(res.toFixed(2));
        }
    }, [context.beforeSts, context.afterSts]);

    useEffect(() => {
        let beforeRows = context.beforeRows;
        let afterRows = context.afterRows;
        // 수축률 = 세탁 전 / 세탁 후 * 100
        if(beforeRows > 0 && afterRows > 0) {
            let res = (beforeRows / afterRows) * 100;
            setContractionRows(res.toFixed(2));
        }

    }, [context.beforeRows, context.afterRows]);

    const handleChangeBeforeStitches = e => {
        context.setBeforeSts(e.target.value);
    };

    const handleChangeBeforeRows = e => {
        context.setBeforeRows(e.target.value);
    };
    const handleChangeAfterStitches = e => {
        context.setAfterSts(e.target.value);
        context.setAfterSts(e.target.value);
    };

    const handleChangeAfterRows = e => {
        context.setAfterRows(e.target.value);
    };

    const swatchInfo = [
        {
            label: "세탁 전",
            txtInfo: [
                {
                    id: "beforeSts",
                    txtLabel: "(숫자만)",
                    value: context.beforeSts,
                    type: "number",
                    onChange: handleChangeBeforeStitches,
                    endAdornment: "코",
                },
                {
                    id: "beforeRows",
                    txtLabel: "(숫자만)",
                    value: context.beforeRows,
                    type: "number",
                    onChange: handleChangeBeforeRows,
                    endAdornment: "단",
                }
            ],
        },
        
        {
            label: "세탁 후",
            txtInfo: [
                {
                    id: "afterSts",
                    txtLabel: "(숫자만)",
                    value: context.afterSts,
                    type: "number",
                    required: true,
                    onChange: handleChangeAfterStitches,
                    endAdornment: "코",
                },
                {
                    id: "afterRows",
                    txtLabel: "(숫자만)",
                    value: context.afterRows,
                    type: "number",
                    required: true,
                    onChange: handleChangeAfterRows,
                    endAdornment: "단",
                }
            ],
        },
    ];


    // const results = [
    //     {
    //         label: "수축률",
    //         results: [
    //             {
    //                 id: "resSts",
    //                 txtLabel: `${contractionSts} %`,
    //                 onChange: null,
    //                 variant: "filled",
    //             },
    //             {
    //                 id: "resRows",
    //                 txtLabel: `${contractionRows} %`,
    //                 onChange: null,
    //                 variant: "filled",
    //             }
    //         ],
    //     },
    // ];

    const results = [
        {
            printDirection: "row",
            sx: {
                bgcolor: "#f5f5f5",
            },
            data: ["수축율", `${contractionSts} %`, `${contractionRows} %`],
        },
    ];

    const rowTitles = ["Swatch", "코 수", " 단 수"];

    return (
        <React.Fragment>
             <InfoTableContainer>
                 <TitleRow titles={rowTitles} />

                {
                    swatchInfo.map((info, idx) => (
                        <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                    ))
                }
                {
                    results.map((res, idx) => (
                        <BasicTable key={idx} items={res.data}
                            printDirection={res.printDirection} sx={{...res.sx}} />

                    ))
                }
                
                 
                {/* <ResultForm label={results[0].label} results={results[0].results} /> */}
            </InfoTableContainer>
        </React.Fragment>
        );
}


export default SwatchTable;