const TitleRow = (props) => {
    const titles = props.titles;
    const colSize = Math.round(12 / titles.length);
    let fontSize = "1em";

    return (
        <React.Fragment>
            <Grid container flexdirection="column" justify="center" justifyContent="center" alignItems="ceneter" spacing={2}>
            {
                titles.map((title, idx) => (
                    <Grid item key={idx} xs={colSize} >
                        <Typography color="primary" align="center" variant="h6" 
                            sx={{ m: 2, fontSize: {fontSize}, fontWeight: 500}} >
                            {title}
                        </Typography>
                    </Grid>
                ))
            }
            </Grid>
        </React.Fragment>
    );
}

export default TitleRow;