import React, { useContext, useEffect, useState } from 'react';
import TableForm from '../Components/TableForm';
import InfoTableContainer from './InfoTableContainer';
import { appContext } from './AppMain';
import { DIVIDE_SLEEVE_COUNT } from '../utils/constants';

const SizeInfo = (props) => {
    const [divSleeves, setdivSleeves] = useState(7); // 소매 나눔 수
    let context = useContext(appContext);

    useEffect(() => {
        let afterSts = context.afterSts;
        let neckBack = context.neckBack;
        // 총 콧수 = 뒷목 둘레 * (세탁후 코수 / 10)
        if(neckBack > 0){
            let totalSts = Math.round(neckBack * (afterSts / 10));
            context.setTotalSts(totalSts);
            context.setTotalSts(totalSts);
        }
            
    }, [context.neckBack, context]);

    useEffect(() => {
        let totalSts = context.totalSts;
        let raglanSts = context.raglanSts;
        if(totalSts > 0){
            if(divSleeves <= 0) setdivSleeves(DIVIDE_SLEEVE_COUNT);
            // 소매 콧수 = (총 콧수 / divSleeves) - (raglanSts / 2)
            // 반올림
            context.setSleeveStartSts(Math.round((totalSts / divSleeves)) - Math.round(raglanSts / 2));
        }

    }, [context.totalSts, divSleeves, context.raglanSts, context]);

    useEffect(() => {
        let totalSts = context.totalSts;
        let sleeveStartSts = context.sleeveStartSts;
        let raglanSts = context.raglanSts;
        let frontStartSt = context.frontStartSt;
        if(totalSts > 0 && raglanSts > 0 && sleeveStartSts > 0) {
            // 뒷판 코수 = 총 코수 - (소매 코수 * 2) - 2(양쪽 앞섶?) - (raglanSts * 4)
            let back = totalSts - (sleeveStartSts * 2) - (raglanSts * 4) - (frontStartSt * 2);
            context.setBackStartSts(back);
        }
    }, [context.totalSts, context.setSleeveStartSts, context.raglanSts, context]);


    const handleChangeNeckBack = e => {
        context.setNeckBack(e.target.value);
        context.setNeckBack(e.target.value);
    }

    const handleChangeRaglanSt = e => {
        context.setRaglanSts(e.target.value);
    }

    const handleChangeSleeves = e => {
        setdivSleeves(e.target.value);
    }

    const sizeInfo = [
        {
            label: "뒷목 둘레",
            txtInfo: [
                {
                    id: "neckBack",
                    txtLabel: "뒷목 둘레를 입력하세요",
                    value: context.neckBack,
                    type: "number",
                    required: true,
                    onChange: handleChangeNeckBack,
                    endAdornment: "cm",
                }
            ],
        },
        
        {
            label: "래글런코",
            txtInfo: [
                {
                    id: "raglanSt",
                    txtLabel: "래글런코를 입력하세요",
                    value: context.raglanSts,
                    type: "number",
                    onChange: handleChangeRaglanSt,
                    endAdornment: "코",
                    required: true,
                },
            ],
        },
        {
            label: "소매 나눔 수",
            txtInfo: [
                {
                    id: "sleeves",
                    txtLabel: "소매 나눔 수를 입력하세요",
                    value: divSleeves,
                    type: "number",
                    onChange: handleChangeSleeves,
                    required: true,
                    
                },
            ],
        },
    ];

    return (
        <React.Fragment>
             <InfoTableContainer>
                 {
                     sizeInfo.map((info, idx) => (
                        <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                     ))
                 }
            </InfoTableContainer>
        </React.Fragment>
        );
}


export default SizeInfo;