import React from 'react';
import { Box } from '@mui/system';
import PropTypes from 'prop-types';


const BasicTable = (props) => {
    const { printDirection="row", items, bg, sx, ...others } = props;

    // 테이블 출력 방향 row 이면 가로로, col 이면 세로로 출력
    // repeat() 의 첫 번째 인자가 1이면 행단 1컬럼만 출력 하므로 세로 출력이 된다.
    let repeat = items.length;
    if(printDirection === "column")  repeat = 1;
    const gridTemplateColumns = `repeat(${repeat}, 1fr)`;


    let colors = {
        bgcolor: 'primary.light',
        color: 'white',
    }

    if(!bg) colors = null;

    return (
        <div style={{ width: '100%' }}>
        <Box
            sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 1,
                gridTemplateColumns: {gridTemplateColumns},
                ...sx,
            }}
            {...others}
        >

        {
            items.map((item, idx)=>{
                return (
                    <Box key={idx}
                        sx={{
                            ...colors,
                            p: 1,
                            borderRadius: 1,
                            textAlign: 'center',
                            fontSize: '1em',
                            fontWeight: '700',
                            ...others,
                        }}
                    >
                        {item}
                    </Box>
            
                );
        })
        }

        </Box>
        </div>
    );
}

BasicTable.propTypes = {
    sx: PropTypes.object,
};

export default BasicTable;