import React, { useContext, useEffect, useState } from 'react';
import InfoTableContainer from './InfoTableContainer';
import { appContext } from './AppMain';
import TitleRow from './TitleRow';
import BasicTable from '../Components/BasicTable';
import TableForm from '../Components/TableForm';
import { FormControlLabel, Radio, RadioGroup } from '@mui/material';
import { infoHighLightStyle, radioButtonStyle } from '../Styles/styles';
import { checkEven } from '../utils/utils';


const RADIO_DESIRED_LENGTH = "length";
const RADIO_DESIRED_ROWS = "rows";

const RADIO_WRIST_SIZE = "size";
const RADIO_WRIST_STITCHES = "stitches";

const SleevesInfo = (props) => {
    let context = useContext(appContext);
    const [selectedDesiredValue, setSelectDesiredValue] = useState(RADIO_DESIRED_LENGTH);
    const [selectedWristValue, setSelectedWristValue] = useState(RADIO_WRIST_STITCHES);
    const [sleeveDecreaseCount, setSleeveDecreaseCount] = useState(0);
    const [sleeveDecreaseRowCount, setSleeveDecreaseRowCount] = useState(0);

      //calculate FB Equal rows & sts
      useEffect(() => {
        let backStartSts = context.backStartSts;
        let frontStartSt = context.frontStartSt;

        if(backStartSts > 0 && frontStartSt > 0) {            
            // 앞단 식: 코 수(y) = 2x(단) + (frontStartSt * 2)(앞섶 시작 코수 양쪽 1씩)
            // 뒷판 식: 코 수(y) = x(단) + backStartSts(뒷판 시작 코 수)
            // ==> 뒤판이 앞판 양쪽 합과 같은 코수가 되는 식: 
            // 2x + (frontStartSt * 2) = x + backStartSts => 
            //  x(fbEqualRows) = backStartSts - (frontStartSt * 2)
            //  y(fbEqualSts) = x(fbEqualRows) + backStartSts
            let rows = backStartSts - (frontStartSt * 2);
            let sts = rows + backStartSts;
            context.setFbEqualSts(sts);
            context.setFbEqualRows(rows);
        }

    }, [context.backStartSts, context.frontStartSt, context.fbEqualSts, context]);

    // 앞뒤판 코 같아진 후에 더 뜰 래글런 길이, 그 후에 소매 코수
    useEffect(() => {
        let afterRows = context.afterRows;
        let sleeveStartSts = context.sleeveStartSts;
        let fbEqualRows = context.fbEqualRows;
        let desiredRaglanLength = context.desiredRaglanLength;
        let desiredRaglanRows = context.desiredRaglanRows;

        if(afterRows > 0 ) {
            let lengthPerRow = 10 / afterRows;
            let rowPerCm = afterRows / 10;

            if(selectedDesiredValue === RADIO_DESIRED_LENGTH) {
                if(desiredRaglanLength > 0) {                    
                    // 원하는 총 래글런 길이에서 이미 뜬만큼의 길이를 빼고 나머지 떠야할 단을 계산
                    let currLength = fbEqualRows * lengthPerRow;
                    let rows = Math.round((desiredRaglanLength - currLength) * rowPerCm); 
                    context.setDesiredRaglanRows(rows);
                }
            } else if(selectedDesiredValue === RADIO_DESIRED_ROWS) {
                if(desiredRaglanRows > 0) {
                    // 총 래글런 길이 구하기
                    // 입력한 단 수에 이미 뜬 만큼의 단을 더한 후 길이 계산
                    let length = Math.round((desiredRaglanRows + fbEqualRows) * lengthPerRow);
                    context.setDesiredRaglanLength(length);
                }
            }
        }


        // 소매 분리 코
        let sleeve = sleeveStartSts;
        if (desiredRaglanRows > 0) {
            // 소매 코수(한 쪽): sleeveStartSts += ( ((fbEqualRows + desiredRaglanLength)) / 2 * 2) 
            // (2단에 2코씩 늘리므로 총 단 수만큼 만 해주면 됨) + raglan 2
            sleeve = sleeveStartSts + (fbEqualRows + desiredRaglanRows) + 2; // 양쪽 래글런에서 하나씩 더 해서 +2
            context.setSleeveSeperateSts(sleeve);

            context.setSleeveTotalSts(sleeve + context.bwlCastOn);
        }

    }, [selectedDesiredValue, context, context.fbEqualRows, context.sleeveStartSts,
         context.afterRows, context.desiredRaglanLength, context.desiredRaglanRows,
         context.bwlCastOn,
        ]);

        // 소매 단 수
        useEffect(() => {
            // 소매길이 * (세탁후 단수 / 10)
            const afterRows = context.afterRows;
            if(afterRows > 0) {
                let sleeveRowCount = context.sleeveLength * (context.afterRows / 10);
                context.setSleeveRowCount(Math.round(sleeveRowCount));
            }

        }, [context, context.afterRows, context.sleeveLength]);


    // 손목 둘레, 손목 콧수
    useEffect(() => {
        if(selectedWristValue === RADIO_WRIST_SIZE){
            let wristSize = context.wristSize;

            if(wristSize > 0 && context.afterSts > 0) {
                let wristSts = Math.round((wristSize * (context.afterSts / 10)));
                context.setWristSts(wristSts);
            }

        } else if(selectedWristValue === RADIO_WRIST_STITCHES){
            let wristSts = context.wristSts;

            if(wristSts > 0 && context.afterSts > 0) { 
                let wristSize = Math.round(wristSts / (context.afterSts / 10));
                context.setWristSize(wristSize);
            }
        }
    }, [selectedWristValue, context, context.wristSize, context.wristSts, context.afterSts]);


    // 소매줄임 계산
    useEffect(() => {
        let decreaseCount = Math.floor((context.sleeveTotalSts - context.wristSts));
        // 한 번에 2코씩 줄이므로 줄임 코수를 짝수로 만들자.
        if(!checkEven(decreaseCount)) decreaseCount -= 1;
        setSleeveDecreaseCount(decreaseCount);

        // 한 번에 2코씩 줄이므로 남은 코를 반으로 나눔
        const decreaseRowCount = sleeveDecreaseCount / 2;
        setSleeveDecreaseRowCount(decreaseRowCount);
    
    }, [sleeveDecreaseCount, context.sleeveTotalStsext, context.sleeveTotalSts, context.wristSts]);




    const handleDesiredRaglanLength = e => {
            context.setDesiredRaglanLength(Number(e.target.value));
    };

    const handleChangeDesiredRaglanRows = e => {
            context.setDesiredRaglanRows(Number(e.target.value));
    };

    const handleRadioDesiredValueChange = e => {
        setSelectDesiredValue(e.target.value);
    };

    const handleSleeveSeperateStsChange = e => {
        // context.setSleeveSeperateSts(e.targe.value);
        console.log("handleSleeveSeperateStsChange:::", e.target.value);
    }

    const handleBWLCastOnChange = e => {
        context.setBwlCastOn(Number(e.target.value));
    }

    const handleSleeveLengthOnChange = e => {
        context.setSleeveLength(Number(e.target.value));
    }

    const handleWristSizeOnChange = e => {
        context.setWristSize(Number(e.target.value));
    }

    const handleWristStsOnChange = e => {
        // console.log("handleWristStsOnChange::::::: ", e.target.value);
        context.setWristSts(Number(e.target.value));
    }

    const handleRadioWristValueChange = e => {
        setSelectedWristValue(e.target.value);
    }

    const handleSleeveTotalOnChange = e => {
        
    }

    const handleSleeveRowCountOnChange = e => {
        // context.setSleeveRowCount(e.target.value);
    }


    const fbInfos = [
        {
            printDrection: "row",
            data: ["앞 뒤 콧수가 같아지는 단", "같아지는 콧수"],
        },
        {
            printDirection: "row",
            sx: infoHighLightStyle,
            data: [`${context.fbEqualRows}단`, `${context.fbEqualSts}코`],
        },
    ];

    // const fbEqualHeader = ["뒷판의 콧수와 앞판 두 개를 더한 콧수가 같을 때까지 뜨기(겉뜨기 단에서만 코 늘림)"];
    // const fbHeader = ["첫 코와 마지막 코 늘림 없이 래글린 앞뒤만 늘리며 계속 뜨기"];
    const headers = {
        fbEqual: ["뒷판의 콧수와 앞판 두 개를 더한 콧수가 같을 때까지 뜨기(겉뜨기 단에서만 코 늘림)"],
        fbHeader: ["첫 코와 마지막 코 늘림 없이 래글린 앞뒤만 늘리며 계속 뜨기"],
        sleeves: ["소매 정보"],
    };

    const moreInfos = [
        {
            label: <FormControlLabel value={RADIO_DESIRED_LENGTH} 
                    control={<Radio/>} 
                    label="총 래글런 길이"
                    sx={ radioButtonStyle(selectedDesiredValue !== RADIO_DESIRED_LENGTH) }
                    />,
            txtInfo: [
                {
                    id: "desireRaglanLegnth",
                    value: context.desiredRaglanLength,
                    txtLabel: "원하는 길이를 입력하면 단 수가 아래에 계산됩니다.",
                    type: "number",
                    required: false,
                    onChange: handleDesiredRaglanLength,
                    endAdornment: "cm",
                    disabled: (selectedDesiredValue === RADIO_DESIRED_LENGTH) ? false : true
                }
            ],
        },
        
        {
            label: <FormControlLabel value={RADIO_DESIRED_ROWS} 
                    control={<Radio />}  
                    label="떠야하는 단 수"
                    sx={ radioButtonStyle(selectedDesiredValue !== RADIO_DESIRED_ROWS) }
                    />,
            txtInfo: [
                {
                    id: "desiredRaglanRows",
                    value: context.desiredRaglanRows,
                    txtLabel: "원하는 단 수를 직접 입력해도 됩니다.",
                    type: "number",
                    required: false,
                    onChange: handleChangeDesiredRaglanRows,
                    endAdornment: "단 더",
                    // disabled: (selectedDesiredValue === RADIO_DESIRED_ROWS) ? false : true,
                    InputProps: {
                        sx: infoHighLightStyle,
                    }
                }
            ],
        },
    ];

    const sleevesInfos = [
        {
            label: "소매 분리 코",
            txtInfo: [
                {
                    id: "sleeveSeperateSts",
                    value: context.sleeveSeperateSts,
                    txtLabel: "소매 분리 코",
                    type: "number",
                    required: true,
                    onChange: handleSleeveSeperateStsChange,
                    endAdornment: "코",
                    disabled: true,
                },
            ]
        },
        {
            label: "감아코",
            txtInfo: [{
                id: "bwlCastOn",
                value: context.bwlCastOn,
                type: "number",
                txtLabel: "짝수로 입력하세요",
                onChange: handleBWLCastOnChange,
                endAdornment: "코",
            },]
        },
        {
            label: "총 소매 코",
            txtInfo: [{
                id: "sleeveTotalSts",
                value: context.sleeveTotalSts,
                type: "number",
                txtLabel: "소매 총 코 수",
                onChange: handleSleeveTotalOnChange,
                endAdornment: "코",
                InputProps: {
                    sx: infoHighLightStyle,
                }
            },]
        },
        {
            label: "소매길이 (고무단 제외)",
            txtInfo: [{
                id: "sleeveLength",
                value: context.sleeveLength,
                type: "number",
                txtLabel: "소매 길이를 입력하세요",
                onChange: handleSleeveLengthOnChange,
                endAdornment: "cm",
            },]
        },
        {
            label: "소매단수",
            txtInfo: [{
                id: "sleeveRowCount",
                value: context.sleeveRowCount,
                type: "number",
                txtLabel: "소매 단 수",
                onChange: handleSleeveRowCountOnChange,
                endAdornment: "단",
                // disabled: true,
                InputProps: {
                    sx: infoHighLightStyle,
                }
            },]
        },
    ];

    const wristInfos = [
        {
            label: <FormControlLabel value={RADIO_WRIST_SIZE} 
                    control={<Radio />} 
                    label="손목둘레"
                    sx={ radioButtonStyle(selectedWristValue !== RADIO_WRIST_SIZE) }
                    />,
             txtInfo: [{
                id: "WristSize",
                value: context.wristSize,
                type: "number",
                txtLabel: "손목 둘레를 입력하세요.",
                onChange: handleWristSizeOnChange,
                endAdornment: "cm",
                disabled: (selectedWristValue === RADIO_WRIST_SIZE) ? false : true,
             }],
        },
        {
            label: <FormControlLabel value={RADIO_WRIST_STITCHES} 
                    control={<Radio />} 
                    label="손목콧수"
                    sx={ radioButtonStyle(selectedWristValue !== RADIO_WRIST_STITCHES) } 
                />,
             txtInfo: [{
                id: "WristSts",
                value: context.wristSts,
                type: "number",
                txtLabel: "손목 콧수를 입력하세요.",
                onChange: handleWristStsOnChange,
                endAdornment: "코",
                // disabled: (selectedWristValue === RADIO_WRIST_STITCHES) ? false : true,
                InputProps: {
                    sx: infoHighLightStyle,
                }
             }],
        }
    ];

    // let sleeveDecreaseCount = Math.floor((context.sleeveTotalSts - context.wristSts));
    // // 한 번에 2코씩 줄이므로 줄임 코수를 짝수로 만들자.
    // if(!checkEven(sleeveDecreaseCount)) sleeveDecreaseCount -= 1;
    // // 한 번에 2코씩 줄이므로 남은 코를 반으로 나눔
    // const decreaseRowCount = sleeveDecreaseCount / 2;  
    // const sleeveRow
    const sleeveDecreaseInfo = [
        {
            prindDirection: "row",
            sx: infoHighLightStyle,
            data: [`소매 줄임: ${sleeveDecreaseCount}코`, `${sleeveDecreaseRowCount} 단마다 2 코씩 줄임`],
        },
    ];

  

    return (
        <React.Fragment>
            {/* 앞 뒤 콧수 같아지는 단 */}
            <InfoTableContainer>
                <TitleRow titles={headers["fbEqual"]} />
                {
                    fbInfos.map((info, idx) => {
                        return (<BasicTable key={idx} items={info.data} bg={info.bg} sx={info.sx} />)
                    })
                }
            </InfoTableContainer>

            {/* 래글런 길이 */}
            <InfoTableContainer>
                <TitleRow titles={headers["fbHeader"]} />
                <RadioGroup row
                    aria-label="desiredRaglanValue"
                    name="desiredRaglanValue"
                    value={selectedDesiredValue}
                    onChange={handleRadioDesiredValueChange}
                >

                {
                moreInfos.map((info, idx) => (
                    <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                ))
                }
            </RadioGroup>
            </InfoTableContainer>

            {/* 소매 정보 */}
            <InfoTableContainer>
                <TitleRow titles={headers["sleeves"]} />
                {
                sleevesInfos.map((info, idx) => (
                    <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                ))
                }
            </InfoTableContainer>

            {/* 손목 둘레나 손목 콧수 중 선택 */}
            <InfoTableContainer>
                <RadioGroup row
                    aria-label="wristValue"
                    name="wristValue"
                    value={selectedWristValue}
                    onChange={handleRadioWristValueChange}
                >
                {
                wristInfos.map((info, idx) => (
                    <TableForm key={idx} label={info.label} txtInfo={info.txtInfo} />
                ))
                }
                </RadioGroup>
            </InfoTableContainer>

            {/* 소매 줄임 */}
            <InfoTableContainer>
               {
                sleeveDecreaseInfo.map((info, idx) => (
                    <BasicTable key={idx} items={info.data} sx={info.sx} />
                ))
                }
                </InfoTableContainer>
        </React.Fragment>
    );
}


export default SleevesInfo;