import { FormControlLabel, Radio, RadioGroup } from '@mui/material';
import React, { useContext } from 'react';
import BasicTable from '../Components/BasicTable';
import { radioButtonStyle } from '../Styles/styles';
import { RADIO_SEX_FEMAILE, RADIO_SEX_MAILE } from '../utils/constants';
import { appContext } from './AppMain';
import InfoTableContainer from './InfoTableContainer';


const SexInfo = (props) => {
    const context = useContext(appContext);

    const sexInfo = [
        "성별",
        <FormControlLabel value={RADIO_SEX_FEMAILE} 
            control={<Radio size="small" />} 
            label="여"
            sx={ radioButtonStyle(context.selectedSex !== RADIO_SEX_FEMAILE) }
        />,
        <FormControlLabel value={RADIO_SEX_MAILE} 
            control={<Radio size="small" />} 
            label="남" 
            sx={ radioButtonStyle(context.selectedSex !== RADIO_SEX_MAILE) }
        />,
    ];
    

    const handleRadioChange = e => {
        context.setSelectedSex(e.target.value);
    }

    return (
    <React.Fragment>
         <InfoTableContainer>
        <RadioGroup row
            aria-label="sexInfo"
            name="sexInfo"
            value={context.selectedSex}
            onChange={handleRadioChange}
        >
            <BasicTable items={sexInfo} placeSelf={"center"} />
        </RadioGroup>
        </InfoTableContainer>
    </React.Fragment>
    );
}


export default SexInfo;